// @import "../../_settings.scss";

.Carte-main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
}

.Carte-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;

}

.Carte-header {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 10px;
}

.logo-name-carte {
    margin-top: 2%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 60%;
    width: 60%;
}

.Carte-header-title {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1%;
    margin-bottom: 1%;
    background: $background-reservation;
    background-position: -10em;
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    font-family: $font-1;
    font-size: 500%;
}

.Carte-container-cocktails {
    display: flex;
    justify-content: center;
    align-items: center;
    // margin-left: auto;
    // margin-right: auto;
    flex-direction: column;
    width: 100%;

}

.Carte-container-cocktails-title {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1%;
    margin-bottom: 1%;
    background: $background-reservation;
    background-position: -10em;
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    font-family: $font-1;
    font-size: 300%;
}

.Carte-container-cocktails-listeCocktails-classique,
.Carte-container-cocktails-listeCocktails-creation,
.Carte-container-mocktails-listeMocktails {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 60%;

    @media screen and (max-width: 990px) {
        width: 80%;
    }

    @media screen and (max-width: 700px) {
        width: 100%;
    }

    // @media screen and (max-width: 650px) {

    //         width: 100%;

    // }


}

.Carte-container-cocktails-carte {
    display: flex;
    // justify-content: center;
    // align-items: center;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 1%;
    // padding: 1%;
    width: 100%;
    height: 230px;
    border: $borderStyle;
    border-radius: $borderRadius;
    color: $color-3;

    @media screen and (max-width: 650px) {
        flex-direction: column;
        height: auto;
        width: 250px;
    }
}

.Carte-container-cocktails-carte-img {
    display: flex;
    width: 40%;
    height: 100%;

    @media screen and (max-width: 650px) {
        background-image: cover;
        height: 250px;
        width: 250px;
    }

}

.Carte-container-cocktails-carte-img-size {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    border: $borderStyle;
    border-radius: $borderRadius;
}

.Carte-container-cocktails-carte-texte {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: left;
    width: 70%;
    height: 100%;
    border: $borderStyle;
    border-radius: $borderRadius;
    padding: 2%;
    padding-bottom: 5%;

    @media screen and (max-width: 650px) {
        background-image: cover;
        // height: 250px;
        width: 250px;
    }
}

.Carte-container-cocktails-carte-cat {
    display: flex;
    justify-content: space-between;
    margin-top: 0;
    margin-bottom: 2%;

    @media screen and (max-width: 650px) {
        margin-bottom: 0%;
        ;
    }

}

