@import "../../_settings.scss";

.Presta-main {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: $borderStyle;
    border-radius: $borderRadius;
    // height: 100vh;
    width: 80%;
    margin-bottom: 2%;
    @media screen and (max-width: 768px) {
    width: 100%;
    }
}



.Presta-block-title {
    display: flex;
    width: 100%;
    height: 20%;
    padding: 1%;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-family: $font-2;
    font-style: italic;
    font-weight: bolder;
    font-size: large;
    color: $color-3;
}

.Presta-block-title-h1 {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    // border: $borderStyle;
    // border-radius: $borderRadius;
}

