.Presta-block-decription {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 25%;
    padding: 5px;
    border: $borderStyle;
    border-radius: $borderRadius;

}

.Presta-block-decription-h2 {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 100%;
    color: $color-2;
}


.Presta-block-decription-p {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding-top: 5px;
    padding-bottom: 10px;
    width: 100%;
    height: 100%;
    color: $color-2;
    font-size: larger;
    font-weight: 700;
    font-kerning: none;
    font-family: $font-2;
}



.Presta-footer-navlink {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    margin-bottom: 10px;
    width: 60%;
    height: 100%;
    @media screen and (max-width: 480px) {
    width: 90%;
    
    }

}


.Presta-footer-navlink-reseau {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    width: 100%;
    max-width: 900px;
    height: 100%;
    border: $borderStyle;
    border-radius: $borderRadius;
    color: $color-3;
    padding: 2%;
    @media screen and (max-width: 970px) {
        flex-direction: column;
        max-width: 350px;
    }
         
}


.Presta-footer-navlink-reseau-span {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    width: 100%;
    height: 100%;
    animation: form-btn 4s linear infinite;
    // padding-bottom: 1%;
        @media screen and (max-width: 970px) {
                padding-bottom: 2%;
            }

}

.Presta-footer-navlink-logo {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;
    height: 100%;
    // padding-bottom: 1%;
    // border: $borderStyle;
    // border-radius: $borderRadius;
}

.Presta-block-decription-Navlink-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 40%;
    height: 40px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding: 2%;
        border: $borderStyle;
            border-radius: $borderRadius;
}

