.Presta-block-decription-btn-form-mod {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 40%;
    height: 40px;
    padding-top: 5px;
    padding-bottom: 5px;
    color: $color-3;
    cursor: pointer;
    padding: 3%;
        border: $borderStyle;
            border-radius: $borderRadius;
}

.Presta-block-decription-btn-form-mod-span {
    display: flex;
    align-items: center;
    justify-content: center;
    // margin: 10%;
}



