.Presta-footer-social-icon-fb {
    width: 70%;
    height: 70%;
    cursor: pointer;
}

.Presta-footer-social-icon-insta {
    width: 80%;
    height: 80%;
    cursor: pointer;
}

.Presta-footer-logo-facebook {
    width: 40px;
    height: 40px;
    border: $borderStyle;
    border-radius: $borderRadius;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #3b5998;
}

.Presta-footer-logo-insta {
    width: 40px;
    height: 40px;
    border: $borderStyle;
    border-radius: $borderRadius;
    display: flex;
    justify-content: center;
    align-items: center;
    background: $background-logo-instagram
}

.Presta-block-photo-grid {
    width: 100%;
  height: 80%;  
    border: $borderStyle;
    border-radius: $borderRadius;
    background-color: $color-3;
    // margin-bottom: 2%;


        @media screen and (max-width: 768px) {
                width: 100%;
                background-color: $color-3;
                border: $borderStyle;
                border-radius: $borderRadius;
        }
                }
                
.Prestation-button-galerie{
display: flex;
justify-content: center;
margin-top: 1%;
margin-bottom: 1%;
align-items: center;
width: 50%;
height: 40px;
font-family: $font-2;
font-weight: 400;
font-size: 1.2rem;
background: $color-3;
border: $borderStyle;
border-radius: $borderRadius;

}